
.backgroundAnimation {
  position: absolute;
  display: block;
  min-width: 100%;
  min-height: 100%;
  top:0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -200;
}

.vanta { 
  min-height: 300px;
  color: white;
  padding: 20px;
  font-size: 24px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
