
{/*The Main Toolbar CSS********************************************************************************************************/}
.nav-bar {
	bottom: 0;
	width: 100%;
	height: 10%;
	background: #000000;
	color: #E6E4E4;
	display: flex;
	position: fixed;
	align-items: center;
	z-index: 15;
	border-top: solid;
	border-top-width: 2px;
	border-top-color: white;
}

.nav-items ul{
	display: flex;
	list-style: none;
	font-size: 150%;
}

.nav-items ul li {
	padding: 0 20px;
}

.nav-items ul li:hover {
	cursor: pointer;
	color: yellow;
}

.nav-items a {
	color: #E6E4E4;
	text-decoration: none;
}

.nav-items a:hover {
	cursor: pointer;
	color: yellow;
}






{/*Contact Bar CSS*******************************************************************************************************/}
.contact-nav-bar {
	height: 20%;
	background-color: black;
	color:white;
	position: fixed;
	z-index: 300;
	bottom:0;
	width:100%;
	transform:translateY(100%);
	transition: transform 0.7s;
	border-top: solid;
	border-top-width: 2px;
	border-top-color: white;
}

.marginLeftForContactTitle {
	width: 80%;
	margin-left: 8vw;
}

.contact-nav-bar.open{
	/*show the nav bar when this style is called*/
	transform:translateY(0);
}

.bigX {
	color:red;
	cursor: pointer;
}

.bigX:hover {
	background:white;
	cursor: pointer;
}

.backdrop{
	height: 100%;
	width: 100%;
	background: rgb(0,0,0,0.7);
	bottom:0;
	position: fixed;
	z-index: 200;
}

.contactItems {
	width:60%;
	padding: 2%;
}

{/*
	for hiding/showing th Nav bar between mobile and desktop mode
@media (max-width: 702px){
	.nav-items{display: none;}
}

@media (min-width: 703px){
	.nav-icon{display: none;}*/
}







{/*Project toolbar CSS***********************************************************************************************************/}
.project-nav-bar {
	height: 90%;
	width: 40%;
	top:0;
	right:0;
	background-color: black;
	color:white;
	position: fixed;
	z-index: 10;
	transform:translateX(100%);
	transition: transform 0.7s;
}

.project-nav-bar-inner {
	height: 100%;
	width: 100%;
	top:0;
	right:0;
	background-color: black;
	color:white;
	position: fixed;
	z-index: 10;
	overflow-y: scroll;
}

.project-nav-bar.open{
	/*show the nav bar when this style is called*/
	transform:translateX(0%);
}


.proColumn1 {
	height: 13%;
	position: fixed;
}

.proColumn2 {
	height: 2%;
}

.proColumn3 {
	margin-top: 15vh;
	height: 85%;
}

.projectSelectTest {
	outline: solid;
	outline-color: red;
}

.projectSelectionButton {
	cursor: pointer;
	background: #828282;
	border: solid;
	border: outset 4px;
	border-color: #7B7A7A;
	border-left-width: 3px;
	border-bottom-width: 3px;
	font-size: 2.5vh;
}

.projectSelectionButton:hover {
	background: #808800;
	border: solid;
	border: outset 4px;
	border-color: #6C7302;
	transition-duration: 0.3s;
	border-left-width: 3px;
	border-bottom-width: 3px;
	font-size: 2.5vh;
}

.projectSelectionButtonSelected {
	cursor: pointer;
	border: inset 4px #B80B0B;
	background: #8B0404;
	border-right-width: 3px;
	border-top-width: 3px;
	font-size: 2.5vh;
}

.toggleButton{
	transform:translateX(-100%);
	height:100%;
	width: 10%;
	top:0%;
	background-color: grey;
	opacity: 0.3;
	z-index: 10;
}

.toggleButton:hover {
	background:white;
}

.toggleArrows{
	position:relative;
	top: 35%;
}
{/*Note: there's also buttonTest in the experience window*/}






{/*The Project window CSS************************************************************************************************************/}

.windowDesignProjectOpen { /*this is if the project tool bar is closed*/
	outline: solid;
	background-color: #646464;
	color: black;
	height:67vh;
	width: 90vw;
	text-align: center;
	position: fixed;
	left:5vw;
	z-index: -5;
	opacity: 0.8;
	overflow-y: scroll;

}

.windowDesignProjectClosed { /*this is if the project tool bar is open*/
	outline: solid;
	background-color: #646464;
	color: black;
	height: 67vh;
	width: 50vw;
	text-align: center;
	position: fixed;
	left:5vw;
	z-index: -5;
	opacity: 0.8;
	overflow-y: scroll;
}

.projDescription {
	white-space: pre-wrap;
	font-size: 3vh;
}









{/*The Experience window CSS****************************************************************************************************/}
.windowDesignExperience {
	outline: solid;
	background-color: #646464;
	color: black;
	height:70vh;
	width: 90vw;
	text-align: center;
	position: fixed;
	left:5vw;
	z-index: -5;
	opacity: 0.8;
}

.windowDesignExperience li {
	text-align: left;
	font-size: 2.8vh;
	padding-top: 1%;
}

.windowDesignExperience li li {
	text-align: left;
	font-size: 2.2vh;
}

.experienceView {
	background-color: white;
}

.buttonTest { /*these buttonTests are also used for the project tool bar*/
	background-color: black;
  	color: white;
  	cursor: pointer;
	transition-duration: 0.4s;
}

.buttonTest:hover {
	background: #808800;
	transition-duration: 0.3s;
}

.buttonTestSelected {
	cursor: pointer;
	background: #8B0404;
	color: white;
}

.expColumn1 {
	height: 15%;
}
.expColumn2 {
	height: 85%;
	overflow-y: scroll;
}

.mobileExpPic { /*removes the pic on mobile version*/
 	width: 40%
}

.mobileExpText {
	width: 60%;
}








{/*The Home page CSS. Mostly in relation to the background, page titles, etc ************************************************************/}


.titleBox {
	position: fixed;
	color:white;
	top:30%;
	min-width: 100%;
	min-height: 100%;
	padding-right: 10%;
	padding-left: 10%;
}

.titleText {
	text-align: left;
	font-size: 200%;
	margin-left: 25%;
}

.projectTitle{
	color:white;
	font-size: 200%;
	top:50%;
	left:50%;
	padding-left: 5%;
	text-align: left;
}

.homeButton a { /*these are on the home page, under the 'hello' function*/
	text-align: left;
	font-size: 150%;
	margin-left: 25%;
	color: #E6E4E4;
	text-decoration: none;
	border-style: solid;
	padding: 10px;
}

.homeButton a:hover {
	cursor: pointer;
	color: yellow;
}

div {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}



































